import request from "@/utils/request";
import store from '@/config/store';
import md5 from '@/utils/md5';
import {
	utf8
} from '@/utils/utils';
import base from '@/config/baseUrl';
let $http = new request({
	baseUrl: base.baseUrl,
	fileUrl: base.imgBaseUrl,
	headers: {
		'content-type': 'application/json;charset=UTF-8'
	}
});
let requestNum = 0;
//
$http.requestStart = function(options) {
	if (requestNum <= 0) {
		uni.showNavigationBarLoading();
		if (options.load) {
			//
			uni.showLoading({
				title: '',
				mask: true
			});
		}
	}
	requestNum += 1;
	var callbackData = {};
	//token
	if (store.state.userInfo.token) {
		options.headers['user_token'] = store.state.userInfo.token;
	};
	console.log("", options);
	return options;
}
//
$http.requestEnd = function(options, resolve) {
	//
	requestNum = requestNum - 1;
	if (requestNum <= 0) {
		uni.hideLoading();
		uni.hideNavigationBarLoading();
	}
}
let loginPopupNum = 0;
//
$http.dataFactory = function(options, resolve) {
	console.log("", resolve);
	//
	var callback = {
		success: false,
		result: ""
	};
	//
	if (resolve.data.success) {
		callback.success = true;
		callback.result = resolve.data.data;
	} else if (resolve.data.code == "1000" || resolve.data.code == "1001") { //code == 10403 
		var content = '';
		if (resolve.data.code == "1000") {
			content = '';
		}
		if (loginPopupNum <= 0) {
			loginPopupNum++;
			if($http.openLogin){
				uni.showModal({
					title: '',
					content: content,
					confirmText: "",
					cancelText: "",
					success: function(res) {
						loginPopupNum--;
						if (res.confirm) {
							//
							$http.openLogin();
						}
					}
				});
			}else{
				loginPopupNum--;
				uni.showToast({
					title: content,
					icon: "none",
					duration: 3000
				});
			}
		}
	} else { //
		if (options.isPrompt) {
			setTimeout(function() {
				uni.showToast({
					title: resolve.data.info,
					icon: "none",
					duration: 3000
				});
			}, 500);
		}
		callback.result = resolve.data;
	}
	return callback;
};
export default $http;
