import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
import {
	modifyJson
} from '@/utils/utils';
//
const state = {
	//
	userInfo: {},
	//webView
	webViewUrl:"",
};
//
const cacheNameList = [];
const mutations = {
	//
	setCacheData(state) {
		for (let name of cacheNameList) {
			let data = uni.getStorageSync(name);
			if (data) {
				state[name] = data;
			}
		}
	},
	//
	setUserInfo(state, data) {
		if (data) {
			state.userInfo = modifyJson(data, state.userInfo);
		}
	},
	//WebView
	setWebViewUrl(state, data) {
		if (data) {
			state.webViewUrl = data;
		}
	},
};
//
const actions = {};
export default new Vuex.Store({
	state,
	mutations,
	actions
});
