/**
 * json
 */
export const modifyJson = (json, oldJson) => {
  if (!json && !oldJson) { return; }
  if (typeof json !== "object") {
    json = JSON.parse(json);
  }
  if (typeof oldJson !== "object") {
    oldJson = JSON.parse(oldJson);
  }

  var jsonData = {};
  for (var i in oldJson) {
    jsonData[i] = oldJson[i];
  }
  for (var j in json) {
    jsonData[j] = json[j];
  }

  return jsonData;
}
/**
 * XX
 */
export const clickDateDiff = function (value) {
  var result;
  var minute = 1000 * 60;
  var hour = minute * 60;
  var day = hour * 24;
  var month = day * 30;
  var now = new Date().getTime();
  var diffValue = parseInt(now) - parseInt(value);
  if (diffValue < 0) {
    return;
  }
  var monthC = diffValue / month;
  var weekC = diffValue / (7 * day);
  var dayC = diffValue / day;
  var hourC = diffValue / hour;
  var minC = diffValue / minute;
  if (monthC >= 1) {
    result = "" + parseInt(monthC) + '';
  } else if (weekC >= 1) {
    result = "" + parseInt(weekC) + '';
  } else if (dayC >= 1) {
    result = "" + parseInt(dayC) + '';
  } else if (hourC >= 1) {
    result = "" + parseInt(hourC) + '';
  } else if (minC >= 1) {
    result = "" + parseInt(minC) + '';
  } else {
    result = '';
  }
  return result;
};
